#aboutContainer {
  width: 100;
}

.hero {
  height: auto;
  width: 100%;
  background-size: cover;
  background-position: center;
}

/* .hero::after {
  display: block;
  opacity: .5;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
} */

.hero * {
  position: relative;
  z-index: 2;
}

/* ============================================= */

#h1Develop {
  color: rgb(22,25,73);
  text-shadow: 2px 2px 4px rgb(173,175,192);
  font-family: 'Unica One', cursive;
  font-size: 60px;
  margin: 10%;
}

#devPage {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
}

.fa-github, .fa-linkedin {
  margin: 5px;
  font-size: 30pt;
  color: white;
}

#devImg {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}

#devImgLink {
  display: block;
  width: 100%;
  height: auto;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  -webkit-transition: .5s ease;
  -o-transition: .5s ease;
  transition: .5s ease;
  background-color: rgb(22,25,73);
}

.developer-image:hover .overlay {
  opacity: 1;
}

#devText {
  color: white;
  font-size: 16px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding-left: 10%;
  padding-right: 10%;
  font-family: 'Quicksand', sans-serif;
}

#devCard {
  width: 375px;
  height: 375px;
  margin: 1%;
}

#devName {
  font-family: 'Dancing Script', cursive;
  font-size: 20pt;
}

#devBio {
  font-family: 'Quicksand', sans-serif;
}

#howItWorksH1, #missionH1 {
  font-family: 'Unica One', cursive;
}




