    padding-top: 20px;
    color: rgb(22,25,73);
}

#surveyFormQuestions {
    font-family: 'Quicksand', sans-serif;
}

#surveyJumbotron {
    color: white;
    background-color: rgb(22,25,73);
    border-radius: 15px 50px;
    width: 100%;
    margin-top: 30px;
